*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body{
  background-color: #eaeef1;
  padding:0;
  margin:0;
  font-family: 'Roboto', sans-serif;

}

p{
  margin:0;
}

.App {
  text-align: center;

}

.ThingsHappening{
  background-color:#fff;
  padding:20px;
  width:calc(100%-40px);
  border-radius:5px;
  display:block;
  margin: 20px 20px 50px;
  padding:30px;
  position:relative;
}

.goBack{
  color:#fff;
  background-color:#620ff8;
  border-radius: 5px;
  padding:0 20px;
  line-height:30px;
  -webkit-appearance:none;
     -moz-appearance:none;
          appearance:none;
  font-weight:bold;
  position:absolute;
  left:10px;
  top:10px;
  cursor: pointer;
}

h3{
  color: #620ff8;
  border-bottom: 1px solid #eaeef1;
  display:block;
  font-size:34px;
  padding-bottom:20px;
  margin: 0 0 30px;
}
h4{
  color: #620ff8;
  border-bottom: 1px solid #eaeef1;
  display:block;
  font-size:34px;
  padding-bottom:0;
  margin: 0 0 20px;
}
h3.solved{
  font-size:24px;
  padding: 0 40px 20px;
}
label{
  color: #620ff8;
  font-size:14px;
  display:block;
  text-align:left;
  font-weight: bold;
}

input[type="text"]{
  width:100%;
  border-radius: 5px;
  margin-bottom:10px;
  height:35px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border:2px solid #eaeef1;
  font-size: 14px;
  position: relative;
}

input[type="text"]+p{
  opacity:0;
  display: inline-block;
  color:red;
  padding:0;
  position: relative;
  font-size:10px;
  top:-5px;
  float:left;
  height:0px;
  width:100%;
  text-align:right;
  -webkit-transition: opacity .2 ease-in;
  -o-transition: opacity .2 ease-in;
  transition: opacity .2 ease-in;
}

input[type="text"].error {
  border:2px solid red;
  -webkit-transition: opacity .2 ease-in;
  -o-transition: opacity .2 ease-in;
  transition: opacity .2 ease-in;

}

input[type="text"].error+p{
  opacity: 1;
}

section{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
}

figure{
  padding:0;
  margin:0;
  -ms-flex: 0 0 100%;
      flex: 0 0 100%;
}
figure>div{
  margin:10px 0;
}

figure p, figure strong{
  text-align: left;
  padding-left:20px;
  display: block;
  margin:0;
}

.goGetIt{
  color:#fff;
  background-color:#620ff8;
  width:75%;
  height:100px;
  font-size:27px;
  border-radius: 5px;
  cursor: pointer;
  font-weight:bold;
}

@media screen and (min-width:1024px) {
  h3.solved{
    font-size:34px;
  }
  figure{
    -ms-flex: 0 0 50%;
        flex: 0 0 50%;
    padding: 0 15px;
    font-size:22px;
  }

  figure strong, figure strong p{
    margin-top:10px;
    line-height:32px;
  }

  .goGetIt{
    width:40%;
  }


}


